.auth {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}


.input:hover {
  outline: none;
  border: 2px solid #ffffff;
}

.authTable {
  width: 22.5%;
  text-align: center;
  border-radius: 20px;
  box-shadow:
    0 20px 40px rgba(0, 0, 0, 0.6),
    0 0 36px rgba(0, 0, 0, 0.22);
}

img:hover {
  transition: 0.3s;
  transform: scale(1.2);
}

.authTable.lightAccent td,
.authTable.lightAccent h1,
.authTable.lightAccent td a {
  background-color: #ffffff;
}

.authTable.darkAccent td,
.authTable.darkAccent h1,
.authTable.darkAccent td a {
  background-color: #181a1b;
}

.authTable h1 {
  position: relative;
  top: -10px;
  font-size: 36px;
}

.authTable.darkAccent {
  color: white;
}
.authTable.lightAccent{
  color: black;
}


.errorCell {
  height: 30px;
  text-align: center;
  background-color: #ffffff;
  color: rgb(255, 0, 0);

  opacity: 0;
  visibility: hidden;

  transition: 0.4s ease;
}

.errorCell.show {
  opacity: 1;
  visibility: visible;
}

.switchAuth a:hover {
  color: #153a8b;
  transition: 0.2s;
}

.switchAuth a {
  transition: 0.2s;
  color: #2563eb;
}

.secret {
  cursor: pointer;
  position: absolute;
  right: 1.5%;
  top: 40%
}

.authInputs {
  padding-left: 5%;
  padding-right: 5%;
  margin: 0;
}

.authInputs:focus {
  border: 2px solid #2563eb;
  transition: 0.3s;
}

tbody h2 {
  top: -20px;
  position: relative;
  color: #555555 !important;
  font-weight: normal;
  font-size: 20px
}

tbody .authHead {
  padding-bottom: 0;
}

.addbtn {
  width: 100%;
  color: white;
}